<template>
  <v-container>
    <PageTitle title="Statistik Tryout" />
    <v-row>
      <v-col cols="12">
        <v-card>
          <v-card-title>
            Riwayat TryOut
            <v-spacer></v-spacer>
            <v-text-field
              v-model="searchInput"
              append-icon="mdi-magnify"
              label="Cari"
              single-line
              hide-details
            ></v-text-field>
          </v-card-title>
          <v-data-table
            :headers="tableHeaders"
            :items="tryoutHistory"
            :search="searchInput"
            :items-per-page="5"
          ></v-data-table>
        </v-card>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12">
        <v-card class="mx-auto">
          <v-card-title>Grafik Capaian</v-card-title>
          <v-sheet class="v-sheet--offset mx-auto" color="primary">
            <v-sparkline
              auto-draw
              :labels="chartLabels"
              :value="chartValues"
              color="white"
              line-width="3"
              padding="16"
              :show-labels="true"
              :smooth="true"
              stroke-linecap="round"
            ></v-sparkline>
          </v-sheet>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
import TableHeaderModel from "@/models/tableHeaderModel";
import TryoutHistoryModel from "@/models/tryoutHistoryModel";
import componentMixin from "@/mixins/componentMixin";
import PageTitle from "./PageTitle.vue";

export default {
  name: "UserStatistic",
  components: { PageTitle },
  data: () => ({
    searchInput: "",
    pageName: "Statistik",
    tableHeaders: [
      new TableHeaderModel("Nama Tryout", "start", true, "tryoutName"),
      new TableHeaderModel("Passing Grade", "start", true, "passingGrade"),
      new TableHeaderModel("Skor", "start", true, "score"),
    ],
    tryoutHistory: [
      new TryoutHistoryModel("Tryout 1", "90%", "90%"),
      new TryoutHistoryModel("Tryout 2", "90%", "90%"),
      new TryoutHistoryModel("Tryout 3", "90%", "90%"),
      new TryoutHistoryModel("Tryout 4", "90%", "90%"),
      new TryoutHistoryModel("Tryout 5", "90%", "90%"),
    ],
    chartLabels: [
      "Tryout 1",
      "Tryout 2",
      "Tryout 3",
      "Tryout 4",
      "Tryout 5",
      "Tryout 6",
      "Tryou5 7",
      "Tryout 8",
    ],
    chartValues: [200, 675, 410, 390, 310, 460, 250, 240],
  }),
  created() {
    this.notifyPageChanged(this.pageName);
  },
  mixins: [componentMixin],
};
</script>